import * as core from "@actions/core";
const isDebug = core.getBooleanInput("debug");
export const logger = {
    info: (message) => {
        core.info(message);
    },
    debug: (message) => {
        if (isDebug) {
            core.debug(message);
        }
    },
    error: (message) => {
        core.error(message);
    },
};
